.hero-banner {
  position: relative;
  background: url(../img/banner/hero-banner.png) left center no-repeat;
  background-size: cover;
  height: 400px;
  z-index: 1;
}
@media (min-width: 1000px) {
  .hero-banner {
    height: 550px;
  }
}
.hero-banner--sm {
  height: 300px;
}
@media (min-width: 1000px) {
  .hero-banner--sm {
    height: 400px;
  }
}
.hero-banner::after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(1, 3, 15, 0.2);
  z-index: -1;
}
.hero-banner__content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.hero-banner__content > * {
  color: #fff;
}
.hero-banner__content h3 {
  font-size: 27px;
  margin-bottom: 0;
}
.hero-banner__content h1 {
  margin-bottom: 12px;
}
.hero-banner__content h4 {
  font-size: 18px;
}
.banner-breadcrumb {
  display: inline-block;
}
.banner-breadcrumb .breadcrumb {
  background: transparent;
  padding: 0;
}
.banner-breadcrumb .breadcrumb-item {
  padding: 0.1rem;
}
.banner-breadcrumb .breadcrumb-item a {
  text-transform: capitalize;
  color: #fff;
}
.banner-breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  color: #fff;
  padding-right: 0.1rem;
  content: "-";
}
.banner-breadcrumb .breadcrumb-item.active {
  color: #fff;
}
.blog__slide {
  border: 0;
  border-radius: 0;
  background-color: transparent;
}
.blog__slide__content {
  padding: 30px 15px;
}
.blog__slide__content h3,
.blog__slide__content a {
  color: #3a414e;
}
.blog__slide__content p {
  font-size: 15px;
  margin-bottom: 0;
}
.blog__slide__label {
  font-size: 13px;
  color: #ff9907 !important;
  text-transform: uppercase;
  background: #222;
  padding: 4px 40px;
  margin-bottom: 18px;
}
.blog-slider {
  position: relative;
}
.blog-slider .blog-slider__leftArrow {
  position: absolute;
  top: 22%;
  left: -12px;
}
@media (min-width: 1250px) {
  .blog-slider .blog-slider__leftArrow {
    left: -105px;
    padding: 0 25px 3px 10px;
  }
  .blog-slider .blog-slider__leftArrow::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: #f5f9fc;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: background 0.4s ease-out;
  }
  .blog-slider .blog-slider__leftArrow::before {
    content: "";
    display: block;
    width: 40px;
    height: 130%;
    background: #fff;
    position: absolute;
    top: -2px;
    left: -15px;
    z-index: 2;
    transform: rotate(-15deg);
  }
  .blog-slider .blog-slider__leftArrow:hover::after {
    background: #222;
  }
}
.blog-slider .blog-slider__rightArrow {
  position: absolute;
  top: 22%;
  right: -12px;
}
@media (min-width: 1250px) {
  .blog-slider .blog-slider__rightArrow {
    right: -105px;
    padding: 0 25px 3px 20px;
  }
  .blog-slider .blog-slider__rightArrow::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: #f5f9fc;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    transition: background 0.4s ease-out;
  }
  .blog-slider .blog-slider__rightArrow::before {
    content: "";
    display: block;
    width: 43px;
    height: 135%;
    background: #fff;
    position: absolute;
    top: -4px;
    right: -4px;
    z-index: 2;
    transform: rotate(15deg);
  }
  .blog-slider .blog-slider__rightArrow:hover::after {
    background: #222;
  }
}
.blog-slider .blog-slider__leftArrow,
.blog-slider .blog-slider__rightArrow {
  display: none;
}
@media (min-width: 580px) {
  .blog-slider .blog-slider__leftArrow,
  .blog-slider .blog-slider__rightArrow {
    display: block;
  }
}
.blog-slider .blog-slider__leftArrow img,
.blog-slider .blog-slider__rightArrow img {
  position: relative;
  z-index: 10;
}
.causes_slider .owl-dots {
